<template>
  <div class="col-sm-12 pt-3">
    <modalElement></modalElement>
    <button
      id="addbutton"
      type="button"
      class="btn btn-primary"
      @click="modalTypeHandler(1)"
      data-bs-toggle="modal"
      data-bs-target="#staticBackdrop"
    >
      <i class="bi bi-pencil"></i>
    </button>
    <div class="card main-card">
      <ul class="list-group">
        <li
          v-for="event in getData"
          :key="event.timestrip"
          class="lists-group-item flex-container py-1"
        >
          <h3
            class="lists-group-item-heading px-1"
            :style="{ color: event.exesType !== '' ? '#C0392B' : '#145A32' }"
          >
            <i class="bi bi-currency-dollar"></i>
            {{ event.amount }}
          </h3>
          <h5 class="px-2">
            <i class="bi bi-calendar-heart"></i>
            {{ event.dateInput }}
          </h5>
          <h5 class="px-2" v-show="event.moneyTypeInput == '0'">
            <i class="bi bi-card-text"></i>
            <span v-show="event.incomeType == '0'">薪水</span>
            <span v-show="event.incomeType == '1'">投資</span>
            <span v-show="event.incomeType == '2'">其他</span>
          </h5>
          <h5 class="px-2" v-show="event.moneyTypeInput == '1'">
            <i class="bi bi-card-text"></i>
            <span v-show="event.exesType == '0'">伙食費</span>
            <span v-show="event.exesType == '1'">交通費</span>
            <span v-show="event.exesType == '2'">治裝費</span>
            <span v-show="event.exesType == '3'">貸款</span>
            <span v-show="event.exesType == '4'">電話費</span>
            <span v-show="event.exesType == '5'">其他</span>
          </h5>
          <button
            class="btn btn-xs btn-warning mx-2"
            @click="modalTypeHandler(2, event)"
            data-bs-toggle="modal"
            data-bs-target="#staticBackdrop"
          >
            修改
          </button>
          <button
            class="btn btn-xs btn-danger mx-2"
            @click="modalTypeHandler(3, event)"
            data-bs-toggle="modal"
            data-bs-target="#staticBackdrop"
          >
            刪除
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import modalElement from '@/components/ModalElment.vue'
export default {
  components: {
    modalElement
  },
  methods: {
    loadState () {
      if (localStorage.getItem('lists')) {
        this.$store.commit('loadJSON', localStorage.getItem('lists'))
      }
    },
    modalTypeHandler (type, thisArray) {
      if (thisArray === undefined) {
        // 新增塞預設陣列給actions
        thisArray = {
          timestrip: undefined,
          dateInput: new Date(+new Date() + 8 * 3600 * 1000)
            .toISOString()
            .substring(0, 10),
          moneyTypeInput: 1,
          incomeType: '',
          exesType: '',
          amount: undefined,
          flag: 0
        }
      }
      const actionObject = {
        type: type,
        originalArray: thisArray
      }
      this.$store.dispatch('SETACTIONS', actionObject)
    }
  },
  computed: {
    getData () {
      return this.$store.getters.filterList
    }
  },
  mounted () {
    this.loadState()
  }
}
</script>
