<template>
  <div class="col-sm-12 pt-3">
    <div
      class="btn-toolbar justify-content-center"
      role="toolbar"
      aria-label="Toolbar with button groups"
    >
      <div class="btn-group pe-1" role="group" aria-label="First group">
        <button
          type="button"
          class="btn btn-outline-secondary"
          @click="chartType = 1"
        >
          收支總表
        </button>
      </div>
      <div class="btn-group pe-1">
        <button
          type="button"
          class="btn btn-outline-secondary"
          @click="chartType = 2"
        >
          收入分類總表
        </button>
      </div>
      <div class="btn-group pe-1">
        <button
          type="button"
          class="btn btn-outline-secondary"
          @click="chartType = 3"
        >
          支出分類總表
        </button>
      </div>
    </div>
    <div v-show="!noData" id='container'></div>
    <div v-show="noData" class="text-center pt-2">沒有資料</div>
  </div>
</template>
<script>
import * as Highcharts from 'highcharts'

export default {
  data () {
    return {
      shareState: this.$store.state,
      noData: false,
      // 報表種類 0 1:總計 2:收入分類加總 3:支出分類加總
      chartType: 3
    }
  },
  mounted () {
    this.loadState()
    const result = this.shareState.lists.filter((value) => value.flag === 0)
    result.length === 0 ? (this.noData = true) : (this.noData = false)
    this.createChart()
    this.changeData()
  },
  methods: {
    loadState () {
      if (localStorage.getItem('lists')) {
        this.$store.commit('loadJSON', localStorage.getItem('lists'))
      }
    },
    createChart () {
      const chart = Highcharts.chart('container', {
        chart: { type: 'pie' },
        title: { text: '' },
        credits: { enabled: false },
        tooltip: {
          style: {
            color: '#333333',
            cursor: 'default',
            fontSize: '20px',
            whiteSpace: 'nowrap'
          },
          headerFormat: '<b>{point.key}</b><br/>',
          pointFormat: '{point.percentage:.1f}%/{point.y}元'
        },
        legend: {
          itemStyle: {
            color: '#333333',
            cursor: 'pointer',
            fontSize: '20px',
            fontWeight: 'bold',
            textOverflow: 'ellipsis'
          },
          align: 'center',
          verticalAlign: 'middle',
          layout: 'vertical',
          floating: true,
          x: 260,
          itemMarginTop: 5,
          itemMarginBottom: 5,
          labelFormat: '{name} {y}元'
        },
        plotOptions: {
          series: {
            showInLegend: true,
            dataLabels: { enabled: false }
          }
        },
        series: [{
          data: []
        }],
        responsive: {
          rules: [
            {
              condition: { maxWidth: 650 },
              chartOptions: {
                legend: {
                  verticalAlign: 'bottom',
                  layout: 'horizontal',
                  floating: false,
                  x: 0
                }
              }
            },
            {
              condition: { maxWidth: 450 },
              chartOptions: {
                legend: { verticalAlign: 'top' }
              }
            }
          ]
        }

      })
      return chart
    },
    changeData () {
      // 重製
      this.noData = false
      let income = 0
      let expenditure = 0
      // 過濾收入
      const incomeArray = this.shareState.lists.filter((element) => {
        // eslint-disable-next-line eqeqeq
        return element.flag === 0 && element.moneyTypeInput == 0
      })
      // 收入分類與金額總計
      const incomeSortArray = incomeArray.reduce((acc, cur, i) => {
        const item = i > 0 && acc.find(res => res.name === cur.incomeType)
        if (item) {
          item.y += parseInt(cur.amount)
        } else {
          acc.push(
            {
              name: cur.incomeType,
              y: parseInt(cur.amount)
            }
          )
        }
        return acc
      }, [])
      // 過濾支出
      const expenditureArray = this.shareState.lists.filter((element) => {
        return element.flag === 0 && element.moneyTypeInput === 1
      })
      //  支出分類與金額總計
      const expenditureSortArray = expenditureArray.reduce((acc, cur, i) => {
        const item = i > 0 && acc.find(element => element.name === cur.exesType)
        if (item) {
          item.y += parseInt(cur.amount)
        } else {
          acc.push(
            {
              name: cur.exesType,
              y: parseInt(cur.amount)
            }
          )
        }
        return acc
      }, [])
      switch (this.chartType) {
        case 1:
          this.createChart().title.update({ text: '收支總表' })
          // eslint-disable-next-line array-callback-return
          this.shareState.lists.filter((value) => {
            // eslint-disable-next-line eqeqeq
            if (value.flag === 0 && value.moneyTypeInput == 0) {
              income += parseInt(value.amount)
            } else if (value.flag === 0 && value.moneyTypeInput === 1) {
              expenditure += parseInt(value.amount)
            }
          })
          if (income === 0 && expenditure === 0) {
            this.noData = true
          }
          this.createChart().series[0].update({
            data: [
              { name: '收入', y: income },
              { name: '支出', y: expenditure }
            ]
          })
          break
        case 2:
          this.createChart().title.update({ text: '收入分類總表' })
          if (incomeArray.length === 0) {
            this.noData = true
          }
          // eslint-disable-next-line array-callback-return
          incomeSortArray.filter((element) => {
          // 標籤替換中文
            switch (element.name) {
              case '0':
                element.name = '薪水'
                break
              case '1':
                element.name = '投資'
                break
              case '2':
                element.name = '其他'
                break
              default:
                break
            }
          })
          this.createChart().series[0].update({
            data: incomeSortArray
          })
          break
        case 3:
          this.createChart().title.update({ text: '支出分類總表' })
          if (expenditureArray.length === 0) {
            this.noData = true
          }
          // eslint-disable-next-line array-callback-return
          expenditureSortArray.filter((element) => {
            // 標籤替換中文
            switch (element.name) {
              case '0':
                element.name = '伙食費'
                break
              case '1':
                element.name = '交通費'
                break
              case '2':
                element.name = '治裝費'
                break
              case '3':
                element.name = '貸款'
                break
              case '4':
                element.name = '電話費'
                break
              case '5':
                element.name = '其他'
                break
              default:
                break
            }
          })
          this.createChart().series[0].update({
            data: expenditureSortArray
          })
          break
      }
    }
  },
  watch: {
    chartType: function () {
      this.changeData()
    }
  }
}
</script>
