<template>
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">{{ title }}</h5>
      </div>
      <form @submit.prevent="submitForm()">
        <div class="modal-body">
          <div class="input-group mb-3 row">
            <label class="input-group-text col-6" for="dateInput">日期:</label>
            <input
              type="date"
              class="form-control col-6"
              id="dateInput"
              required
              v-model="inputList.dateInput"
              :disabled="disabled"
            />
          </div>
          <div class="input-group mb-3 row">
            <label class="input-group-text col-2" for="options-outlined"
              >種類:</label
            >
            <input
              type="radio"
              class="btn-check"
              name="options-outlined"
              id="success-outlined"
              value="0"
              required
              @click="inputList.exesType = ''"
              v-model="inputList.moneyTypeInput"
              autocomplete="off"
              :disabled="disabled"
            />
            <label class="btn btn-outline-success col-5" for="success-outlined"
              >收入</label
            >
            <input
              type="radio"
              class="btn-check"
              name="options-outlined"
              id="danger-outlined"
              value="1"
              @click="inputList.incomeType = ''"
              v-model="inputList.moneyTypeInput"
              autocomplete="off"
              :disabled="disabled"
            />
            <label class="btn btn-outline-danger col-5" for="danger-outlined"
              >支出</label
            >
          </div>
          <template v-if="inputList.moneyTypeInput == 0">
            <div class="input-group mb-3 row">
              <label class="input-group-text col-6" for="incomeType"
                >收入類型:</label
              >
              <select
                class="form-select col-6"
                aria-label="incomeType"
                required
                v-model="inputList.incomeType"
                :disabled="disabled"
              >
                <option selected value="" disabled>請選擇</option>
                <option value="0">薪水</option>
                <option value="1">投資</option>
                <option value="2">其他</option>
              </select>
            </div>
          </template>
          <template v-else>
            <div class="input-group mb-3 row">
              <label class="input-group-text col-6" for="exesType"
                >支出類型:</label
              >
              <select
                class="form-select col-6"
                aria-label="exesType"
                required
                v-model="inputList.exesType"
                :disabled="disabled"
              >
                <option selected value="" disabled>請選擇</option>
                <option value="0">伙食費</option>
                <option value="1">交通費</option>
                <option value="2">治裝費</option>
                <option value="3">貸款</option>
                <option value="4">電話費</option>
                <option value="5">其他</option>
              </select>
            </div>
          </template>
          <div class="input-group mb-3 row">
            <label class="input-group-text col-6" for="amount">費用:</label>
            <input
              type="number"
              class="form-control col-6"
              id="amount"
              min="1"
              required
              v-model="inputList.amount"
              :disabled="disabled"
            />
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            id="close"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            取消
          </button>
          <button type="submit" class="btn btn-primary">確定</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      shareState: this.$store.state,
      title: '',
      disabled: false,
      inputList: {
        timestrip: undefined,
        dateInput: new Date(+new Date() + 8 * 3600 * 1000)
          .toISOString()
          .substring(0, 10),
        moneyTypeInput: 1,
        incomeType: '',
        exesType: '',
        amount: undefined,
        flag: 0
      }
    }
  },
  methods: {
    closeModal () {
      const modalDom = document.getElementById('close')
      modalDom.click()
    },
    clear () {
      this.inputList.timestrip = undefined
      this.inputList.moneyTypeInput = 1
      this.inputList.dateInput = new Date(+new Date() + 8 * 3600 * 1000)
        .toISOString()
        .substring(0, 10)
      this.inputList.incomeType = ''
      this.inputList.exesType = ''
      this.inputList.amount = undefined
      this.inputList.flag = 0
      console.log('清空')
    },
    submitForm () {
      switch (this.shareState.actionType) {
        case 1:
          this.$store.dispatch('ADD', this.inputList)
          break
        case 2:
          this.$store.dispatch('UPDATE', this.inputList)
          break
        case 3:
          this.$store.dispatch('DELETE', this.inputList)
          break
      }

      this.clear()
      this.closeModal()
      this.saveState()
    },
    saveState () {
      localStorage.setItem('lists', this.$store.getters.toJSON)
    }
  },
  watch: {
    'shareState.actionType': function () {
      const actionType = this.shareState.actionType
      actionType === 1
        ? (this.title = '新增紀錄')
        : actionType === 2
          ? (this.title = '編輯紀錄')
          : (this.title = '刪除紀錄')
      actionType === 1 || actionType === 2
        ? (this.disabled = false)
        : (this.disabled = true)
    },
    'shareState.originalArray.timestrip': function () {
      const actionType = this.shareState.actionType
      actionType === 1 || actionType === 2
        ? (this.disabled = false)
        : (this.disabled = true)
      const originalArray = this.shareState.originalArray
      this.inputList.amount = originalArray.amount
      this.inputList.timestrip = originalArray.timestrip
      this.inputList.moneyTypeInput = originalArray.moneyTypeInput
      this.inputList.dateInput = originalArray.dateInput
      this.inputList.incomeType = originalArray.incomeType
      this.inputList.exesType = originalArray.exesType
      this.inputList.flag = originalArray.flag
    }
  }
}
</script>
